.pass-word {
  padding-top: 100px;
  padding-bottom: 100px;
}
.main-bgc-fp {
  background-color: #fff;
  width: 672px;
  height: 780px;
  margin: 0 auto;
  overflow: hidden;
}
.main-bgc-fp .main-tit {
  font-size: 24px;
  font-weight: bold;
  color: rgba(0, 0, 0, 0.85);
  text-align: center;
  margin-top: 48px;
  margin-bottom: 32px;
}
.main-bgc-fp .main-tabs {
  width: 484px;
  margin: 0 auto;
}
.main-bgc-fp .ant-tabs-tab {
  font-size: 18px;
}
.main-bgc-fp .login-form-button {
  width: 100%;
  height: 60px;
  font-size: 18px;
}
.main-bgc-fp .ant-input-affix-wrapper {
  height: 60px;
}
.main-bgc-fp .margin-b {
  margin-bottom: 9px;
}
.main-bgc-fp .operation {
  width: 100%;
  background-color: yellow;
}
.main-bgc-fp .operation div {
  display: inline-block;
}
.main-bgc-fp .forget-password {
  float: left;
  color: $blue;
}
.main-bgc-fp .forget-register {
  float: right;
}
.main-bgc-fp .forget-register span {
  color: $blue;
}
.main-bgc-fp .ant-tabs-content {
  margin-bottom: 0%;
}
.main-bgc-fp .ant-input-affix-wrapper,
.main-bgc-fp .ant-select-selection--single,
.main-bgc-fp .ant-select-dropdown-menu-item {
  height: 60px;
}
.main-bgc-fp .ant-input-search-button {
  height: 60px;
}
.main-bgc-fp .ant-select-selection-selected-value,
.main-bgc-fp .ant-select-dropdown-menu-item {
  line-height: 60px;
}
@media only screen and (max-width: 768px) {
  .pass-word {
    padding-top: 50px;
    margin-bottom: 50px;
    padding-bottom: 0px;
  }
  .main-bgc-fp {
    background-color: #fff;
    width: 80%;
    height: 100%;
    margin: 0 auto;
    overflow: hidden;
  }
  .main-bgc-fp .main-tit {
    font-size: 24px;
    font-weight: bold;
    color: rgba(0, 0, 0, 0.85);
    text-align: center;
    margin-top: 24px;
    margin-bottom: 10px;
  }
  .main-bgc-fp .main-tabs {
    width: 80%;
    margin: 0 auto;
  }
  .main-bgc-fp .login-form-button {
    width: 100%;
    height: 100%;
    font-size: 18px;
  }
  .main-bgc-fp .ant-input-affix-wrapper {
    height: 100%;
  }
  .main-bgc-fp .ant-tabs-content {
    margin-bottom: 10%;
  }
  .main-bgc-fp .ant-input-affix-wrapper,
  .main-bgc-fp .ant-select-selection--single,
  .main-bgc-fp .ant-select-dropdown-menu-item {
    height: 100%;
  }
  .main-bgc-fp .ant-input-search-button {
    height: 100%;
  }
  .main-bgc-fp .ant-input-search-button {
    height: 32px;
    width: 116px;
  }
  .main-bgc-fp .ant-select-selection-selected-value,
  .main-bgc-fp .ant-select-dropdown-menu-item {
    line-height: 100%;
  }
  .main-bgc-fp .ant-select-selection-selected-value {
    line-height: 30px !important;
  }
}
